html {
  font-size: 10px;
}
body {
  font-size: 1.4rem;
  color: #605F5F;
  font-family: "akkurat", sans-serif;
  background: #f5f7fc;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: grayscale;
  -moz-osx-font-smoothing: grayscale;
}
.hidden, hide {
  display: none;
}
a {
  -webkit-transition: color .3s ease-out;
  transition: color .3s ease-out;
}
a:hover {
  color: #d1434a;
  -webkit-transition: color .3s ease-out;
  transition: color .3s ease-out;
}
/** the page max width **/
.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 10px;
}
.nav-breadcrumb-wrap {
  background: #f0f0f0;
}
/** msg infot tips **/
.lemall-msg-info {
  margin: 20px 0;
}
.lemall-msg-info span {
  position: relative;
  display: inline-block;
  line-height: 1.2em;
  padding: 10px 20px 10px 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background: #fff;
  font-size: 12px;
  color: #d1434a;
}
.lemall-msg-info span:before {
  position: absolute;
  top: 8px;
  left: 20px;
  content: "";
  width: 16px;
  height: 16px;
  background: url(http://image3.lecloud.com/lemallus/20160921-91ac72bb-bcc8-4741-8f66-38c10a105043.png) no-repeat 0 0;
  background-size: 16px auto;
}
/** 面包屑 **/
.nav-breadcrumb {
  padding: 10px 0;
  line-height: 25px;
  font-size: 14px;
}
.nav-breadcrumb a {
  position: relative;
  margin-right: 16px;
  color: #999;
}
.nav-breadcrumb a:after {
  position: absolute;
  top: 3px;
  right: -12px;
  content: "/";
  line-height: 1.2;
}
.nav-breadcrumb a:hover {
  color: #d1434a;
}
.nav-breadcrumb span {
  color: #d1434a;
}
/** 普通页面标题 **/
.page-title-normal {
  font-family: "moderat", sans-serif;
  color: #333;
}
.page-title-normal h2 {
  padding: 40px 0 20px 0;
  font-size: 22px;
  text-transform: uppercase;
  letter-spacing: .25em;
  font-weight: bold;
}
.section-title-normal {
  padding-bottom: 10px;
  font-size: 20px;
}
/** The normal link **/
.a-link-more {
  color: #ee7a23;
  text-decoration: underline;
}
/** no data **/
.no-data {
  font-size: 20px;
  padding: 4.5em 0;
  text-align: center;
  background: #fff;
}
.loading {
  padding: 80px 0;
  background: #fff;
  text-align: center;
}
.loading .loading-inner {
  width: 100px;
  height: 60px;
  margin: 0 auto;
}
.loading .loading-icon {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: url(http://image3.lecloud.com/lemallus/20170104-9f72903f-2c04-46a1-a07f-98fabd211d48.gif) no-repeat 50% 50%;
  background-size: auto 100%;
  text-indent: -8000px;
}
.loading.loading-auto .loading-inner {
  width: 100%;
  height: 100%;
}
.loading.loadding-small .loading-inner {
  width: 50px;
  height: 50px;
}
.view-more-normal {
  /*text-align:center;
    .view-more-btn{
        display:block;
        padding:70px 0;
        color:$orange;
        font-size:14px;
        letter-spacing:3.5px;
        text-transform: uppercase;
        text-decoration: underline;
    }*/
  position: relative;
  width: 100px;
  height: 50px;
  margin: 0 auto 20px auto;
  overflow: hidden;
  text-align: center;
  font-size: 14px;
}
.view-more-normal .view-more-btn {
  position: absolute;
  top: -60px;
  left: 0;
  width: 100px;
  height: 100px;
  padding-top: 60px;
  border-radius: 50%;
  border: 1px solid #ee7a23;
  background: #fff;
  color: #ee7a23;
  z-index: 1;
}
@media only screen and (max-width: 1280px) {
  .container {
    width: 100%;
  }
}
@media only screen and (max-width: 991px) {
  .page-title-normal h2 {
    padding: 30px 0 20px 0;
    font-size: 18px;
  }

  .no-data {
    font-size: 16px;
  }

  .loading {
    padding: 50px 0;
  }
}
@media only screen and (max-width: 767px) {
  html {
    font-size: 12.5px;
  }

  .container {
    padding: 0;
  }
  /** msg infot tips **/
  .lemall-msg-info {
    margin: 10px 0;
  }

  .lemall-msg-info span {
    padding-left: 35px;
    font-size: 1.2rem;
  }

  .lemall-msg-info span:before {
    left: 10px;
  }

  .nav-breadcrumb {
    display: none;
  }

  .page-title-normal {
    text-align: center;
  }

  .page-title-normal h2 {
    padding: 1.3rem 0;
    font-size: 1.4rem;
    border-bottom: 1px solid #e9e9e9;
  }

  .section-title-normal {
    padding: 10px 10px 5px 10px;
    font-size: 1.2rem;
    border-bottom: 1px solid #e9e9e9;
  }

  .no-data {
    font-size: 1.2rem;
  }

  .loading {
    padding: 100px 0;
  }

  .view-more-normal {
    margin-top: 20px;
    margin-bottom: 10px;
    width: 60px;
    height: 60px;
    font-size: 10px;
  }

  .view-more-normal .view-more-btn {
    top: 0;
    width: 60px;
    height: 60px;
    padding-top: 20px;
  }
  /** foot btn fixed **/
  .body-btn-flexed {
    padding-bottom: 50px;
  }

  .foot-btn-fixed {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin: 0;
    z-index: 101;
  }

  .foot-btn-fixed .btn {
    width: 100% !important;
    height: 50px;
    line-height: 50px;
    background: #d1434a;
    color: #fff;
  }
}
@media only screen and (max-width: 640px) {
  html {
    font-size: 11.6667px;
  }
}
@media only screen and (max-width: 375px) {
  html {
    font-size: 10.8333px;
  }
}
@media only screen and (max-width: 320px) {
  html {
    font-size: 10px;
  }
}
.btn--dis-1, .btn.btn--dis, .btn[href].btn--dis {
  border-color: #ccc;
  background: transparent;
  color: #ccc;
  cursor: default;
}
.btn--dis-1:hover, .btn.btn--dis:hover {
  border-color: #ccc !important;
  background: transparent !important;
  color: #ccc !important;
}
.btn--dis-2, .btn--orange.btn--dis, .btn--orange[href].btn--dis, .btn--red.btn--dis, .btn--red[href].btn--dis {
  border-color: #ccc;
  background-color: #ccc;
  color: #fff;
  cursor: default;
}
.btn--dis-2:hover, .btn--orange.btn--dis:hover, .btn--red.btn--dis:hover {
  border-color: #ccc !important;
  background: #ccc !important;
  color: #fff !important;
}
.btn {
  height: 55px;
  line-height: 54px;
  padding: 0 10px;
}
.btn, .btn[href] {
  display: inline-block;
  text-align: center;
  font-size: 14px;
  font-family: "moderat", sans-serif;
  font-weight: bold;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
  border: 1px solid #d1434a;
  color: #d1434a;
  text-transform: uppercase;
  letter-spacing: .25em;
  white-space: nowrap;
  margin-top: 0!important;
}
/*.btn:hover, .btn[href]:hover {
  background-color: #ffe5e6;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
}*/
.btn--orange, .btn--orange[href] {
  background-color: #ee7a23;
  border-color: #ee7a23;
  color: #fff;
}
.btn--orange:hover, .btn--orange[href]:hover {
  background-color: #faa363;
}
.btn--white, .btn--white[href] {
  background: transparent;
  border-color: #fff;
